<template>
	<view class="box">
		<view class="title-box">
			<titleLine :title-list="title_list" @titleChoose="chooseTitle" :curTitle="cur_title"></titleLine>
			<navigator url="/pages/member/identity_add" class="iconfont iconjia">
			</navigator>
		</view>
		<view class="identity-box">
			<mescroll-body ref="mescrollRef" @init="mescrollInit" @down="downCallback" @up="upCallback">
				<view class="identity-li" v-for="(item,k) in data_list" :key="k">
					<view class="top" @tap="chooseIden(k)">
						<view class="mark">
							{{identityType_map[item.identityType]}}
						</view>
						<view class="name" v-if="item.identityType=='PERSONAL'">
							{{item.customerName}}
						</view>
						<view class="name" v-else>
							{{item.enterpriseName}}
						</view>
					</view>
					<view class="bottom">
						<view class="phone" @tap="chooseIden(k)">
							{{item.mobile}}
						</view>
						<view class="right">
							<view @tap="deleteIdentity(k)" class="iconfont iconshanchu text-red">
							</view>
							<view @tap="edit(k)" class="iconfont iconbianji">
							</view>
						</view>

					</view>
				</view>
			</mescroll-body>
		</view>

	</view>
</template>

<script>
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollBody from "@/components/mescroll-uni/mescroll-body.vue"
	import titleLine from '@/components/title-line.vue'
	let from = ""
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				title_list: [{
						title: '全部',
						id: "ALL"
					}, {
						title: '个人',
						id: "PERSONAL"
					},
					{
						title: '企业',
						id: "ENTERPRISE"
					}
				],
				cur_title: "ALL",
				data_list: [],
				identityType_map: {
					"PERSONAL": "个人",
					"ENTERPRISE": "企业",
				},
				canReset: false,
			}
		},
		components: {
			titleLine,
			MescrollBody
		},
		onLoad(option) {
			// 从发布悬赏跳转
			from = option.from;
		},
		onShow() {
			this.canReset && this.mescroll.resetUpScroll()
			this.canReset = true // 过滤第一次的onShow事件,避免初始化界面时重复触发upCallback
		},
		methods: {
			// 从发布悬赏过来选择发布人
			chooseIden(k) {
				if (!from) return;
				let data = this.data_list[k];
				let obj = {
					identityName: data.customerName,
					identitySeqId: data.seqId
				};
				if (data.identityType != 'PERSONAL') {
					obj.identityName = data.enterpriseName
				}
				this.$store.commit('saveIssueIdentity', obj);
				uni.navigateBack()
			},
			// 选择类型
			chooseTitle(e) {
				this.cur_title = e.id;
				this.mescroll.resetUpScroll(true)
			},
			// 删除
			deleteIdentity(k) {
				let self = this;
				uni.showModal({
					title: "温馨提示",
					content: "确认删除?删除后不可恢复",
					success: function(res) {
						if (res.confirm) {
							self.toDelete(k)
						}
					}
				})
			},
			toDelete(k) {
				this.$api.get('/o2oIdentityManage/delete', {
					params: {
						seqId: this.data_list[k].seqId
					}
				}).then(res => {
					if (res.code == 200) {
						this.$box.toast('删除成功')
						this.data_list.splice(k, 1)
					}
				})
			},
			// 编辑
			edit(k) {
				let the_data = this.data_list[k];
				this.$common.setLocal('identity', the_data);
				uni.navigateTo({
					url: "/pages/member/identity_add?type=update"
				})
			},
			upCallback(page) {
				let pageNum = page.num; // 页码, 默认从1开始
				let pageSize = page.size; // 页长, 默认每页10条
				this.$api.get('/o2oIdentityManage/findPage', {
					params: {
						customerSeqId: this.$common.getLocalSync('customerSeqId'),
						identityType: this.cur_title == "ALL" ? "" : this.cur_title,
						pageno: pageNum,
						pagesize: pageSize
					}
				}).then((res) => {
					if (res.code && res.code != 200) {
						this.mescroll.endErr();
						return
					}
					let data = res;
					// 接口返回的当前页数据列表 (数组)
					let curPageData = data.list;
					// 接口返回的当前页数据长度 (如列表有26个数据,当前页返回8个,则curPageLen=8)
					let curPageLen = curPageData.length;
					// 接口返回的总页数 (如列表有26个数据,每页10条,共3页; 则totalPage=3)
					let totalPage = data.pages;

					//设置列表数据
					if (page.num == 1) this.data_list = []; //如果是第一页需手动置空列表
					this.data_list = this.data_list.concat(curPageData)
					// 请求成功,隐藏加载状态
					//方法一(推荐): 后台接口有返回列表的总页数 totalPage
					this.mescroll.endByPage(curPageLen, totalPage);
				})
			}
		}
	}
</script>

<style lang="scss">
	.box {
		width: 100%;
		min-height: calc(100vh - var(--window-top));
		background: white;
	}

	.title-box {
		width: calc(100% - 34rpx);
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1px solid #DEDEDE;
		padding-left: 34rpx;

		.iconfont {
			color: $color-primary;
			font-size: 44rpx;
			padding: 0 34rpx;
		}
	}

	.identity-box {
		width: calc(100% - 68rpx);
		padding: 0 34rpx;
	}

	.identity-li {
		width: 100%;
		border-bottom: 1px solid #DEDEDE;
		display: flex;
		align-content: center;
		flex-wrap: wrap;
		height: 170rpx;

		.top {
			display: flex;
			align-items: center;
			width: 100%;
			margin-bottom: 28rpx;

			.mark {
				width: 58rpx;
				height: 36rpx;
				line-height: 36rpx;
				background: rgba(72, 114, 229, 1);
				border-radius: 4rpx;
				color: white;
				font-size: 22rpx;
				text-align: center;
			}

			.name {
				font-size: 34rpx;
				margin: 0 20rpx;
			}

			.iconfont {
				font-size: 28rpx;
				margin-right: 10rpx;
				color: #999;

				&.active {
					color: #FF4B4B;
				}
			}

			.rz {
				color: #666666;
				font-size: 22rpx;
			}
		}

		.bottom {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: space-between;

			.phone {
				font-size: 30rpx;
				color: #666666;
				margin-left: 53rpx;
			}

			.right {
				display: flex;
			}

			.iconfont {
				font-size: 30rpx;
				padding-left: 10rpx;
				width: 60rpx;
				text-align: center;

			}
		}
	}
</style>
